<template>
  <div v-loading="houseInfoLoading">
    <div style="display: flex;justify-content: flex-end">
      <el-button type="primary" @click="prevFunc">返回</el-button>
    </div>
    <el-tabs v-model="activeTab" @tab-click="handleClick">
      <el-tab-pane label="房屋基本信息" name="basic">
        <basic-info :room-info="roomInfo" @indexInit="init"/>
      </el-tab-pane>
      <el-tab-pane label="房屋生活信息" name="living">
        <living-info :water-trend="waterTrend" />
      </el-tab-pane>
      <el-tab-pane label="调查走访记录" name="visit">
        <visit-info :visit-info="visitInfo" />
      </el-tab-pane>
      <el-tab-pane label="社会面采集信息" name="shopping">
        <shop-info :shop-info="shopInfo" />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import BasicInfo from './BasicInfo'
import LivingInfo from './LivingInfo'
import VisitInfo from './VisitInfo'
import ShopInfo from './ShopInfo'
import Service from '../service'

export default {
  name: 'RoomInfo',
  components: {
    BasicInfo,
    LivingInfo,
    VisitInfo,
    ShopInfo
  },
  data() {
    return {
      activeTab: 'basic',
      proCollId: '',
      roomInfo: {},
      visitInfo: [],
      shopInfo: [],
      waterTrend: {},
      houseInfoLoading:false
    }
  },
  mounted () {
    const { proCollId } = this.$route.query
    this.proCollId = proCollId
    this.init()
  },
  methods: {
    async init() {
      const params = {
        proCollId: this.proCollId
      }
      const docs = ['getRoomInfo', 'getVisitInfo', 'getWaterTrend', 'getShoppingInfo']
      const promises = docs.map(v => Service[v](params))
      this.houseInfoLoading = true
      const res = await Promise.all(promises)
      this.houseInfoLoading = false
      const [roomInfo, visitInfo, waterTrend, shopInfo] = res
      this.roomInfo = roomInfo.data
      this.visitInfo = visitInfo.data
      this.waterTrend = waterTrend
      this.shopInfo = shopInfo
    },
    handleClick(tab, event) {
      // console.log(tab);
    },
    prevFunc(){
      this.$router.go(-1)
    }
  }
}
</script>

<style>
.prev-right-btn {
  position: absolute;
  right: 51px;
  z-index: 3;
}
</style>
