        this.abnormalInfo = this.visitInfo[val].visitReason;
        this.hiddenDanger = this.visitInfo[val].hiddenDanger;
        this.personTagShow = true
      } else {
        this.personTagShow = false
      }
    },
    changeSwitchType: function (d) {
      this.switchType = d
    }
  }
}
