<script>
  import Service from '../service'

  export default {
    name: 'RoomBaseInfo',
    data() {
      return {
        dialogFormVisible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px',
        switchType: '1',
        rules: {
          landlordName: [
            {required: true, message: '请输入房东姓名', trigger: 'blur'},
          ],
          residentId: [
            {required: true, message: '请输入身份证号', trigger: 'blur'}
          ]
        },
      };
    },
    props: {
      roomInfo: Object
    },
    watch: {
      roomInfo: function () {
        if (this.roomInfo.basicInfo != undefined && this.roomInfo.basicInfo == null && this.roomInfo.basicInfo == "") {
          for (let i = 0; i < this.roomInfo.basicInfo.length; i++) {
            this.roomInfo.basicInfo[i] = JSON.parse(this.roomInfo.basicInfo[i])
          }
          for (let i = 0; i < this.roomInfo.residentInfo.length; i++) {
            this.roomInfo.residentInfo[i] = JSON.parse(this.roomInfo.residentInfo[i])
          }
        }
      }
    },
    methods: {
      changeRoomInfo: function (data) {
        this.form = data
        this.dialogFormVisible = true
      },
      // changeSwitchType: function (d) {
      //   this.switchType = d
      // },
      async saveBasicCommunity() {
        let params = {
          proCollId: this.form.proCollId,
          landlordName: this.form.landlordName,
          landlordId: this.form.landlordId,
          houseStatus: this.form.houseStatus,
          id: this.form.id
        };
        const data = await Service.saveBasicCommunity(params);
        this.$refs.communityForm.resetFields()
        this.dialogFormVisible = false;
        this.$emit('indexInit', data);
      },

      submitSave: function () {
        this.$refs.communityForm.validate((valid) => {
          if (valid) {
            this.saveBasicCommunity();
          } else {
            return false;
          }
        })
      }
    }
  }
</script>

